import React from "react";
import InputFileUpload from "../../../../../../../core/ui/components/forms/input-file-upload"
import {Translate} from "react-redux-i18n";

class ActionFileUpload extends React.Component {
    render() {
        let getTranslation = (key) => {
            return "Campaigns.edit.tabs.invites.edit.form.actions.fileUpload." + key;
        };
        let dropZone=(
            <div className="drop-zone">
                <i className="icon fa-photo" aria-hidden="true"/>
                <span>
                    <Translate value={getTranslation("label")}/>
                </span>
            </div>
        );
        let {updateActionValue}=this.props;
        return (
            <div>
                <InputFileUpload content={dropZone}
                                 settings={{multiple: false, maxSize:3 * 1024 * 1024, className:"icon-drop-zone",activeClassName:"icon-drop-zone-active", accept:"image/png, image/jpeg, image/gif"}}
                                 uploader="campaign-image-upload"
                                 callback={()=>{}} input={{onChange:updateActionValue("media"), value: null}}/>
            </div>
        )
    }
}

export default ActionFileUpload;
