import React from "react";
import Aside from "./aside/container";
import Action from "./action/container";

let getTranslation = (key) => {
    return "Campaigns.edit.tabs.invites." + key;
};

class CampaignListView extends React.Component {
    render() {
        let {invites, accounts, teamAttacks,currentInvite, actionIndex,toggleNetwork,updateInviteValue,addInviteOption,goToAction,createInvite,saveInvites, deleteInviteOption}= this.props;

        return (
            <div className="row">
                <Aside accounts={accounts}
                       teamAttacks={teamAttacks}
                       currentInvite={currentInvite}
                       toggleNetwork={toggleNetwork}
                       updateInviteValue={updateInviteValue}/>
                
                <Action invites={invites}
                        currentInvite={currentInvite}
                        actionIndex={actionIndex}
                        updateInviteValue={updateInviteValue}
                        addInviteOption={addInviteOption}
                        deleteInviteOption={deleteInviteOption}
                        goToAction={goToAction}
                        createInvite={createInvite}/>
            </div>
        );
    }
}


export default CampaignListView;

