import React, {Component} from 'react'
import Geosuggest from 'react-geosuggest';

class InputGeolocation extends Component {


    /**
     * When a suggest got selected
     * @param  {Object} suggest The suggest
     */
    onSuggestSelect(suggest, onChange) {
        let val = {
            label: suggest.label,
            location: suggest.location
        };
        this.props.input.onChange(val);
    }

    render() {
        const {input: {value, onChange}} = this.props;

        let geoLabel = this.props.input.value ? this.props.input.value.label : "Enter Location";
        return (
            <div>
                <Geosuggest placeholder={geoLabel}
                            types={this.props.types}
                            onSuggestSelect={(val) => {onChange({label: val.label, geolocation: val.location})}} />
            </div>
    )
    }
}

export default InputGeolocation