import React from "react";
import {Translate, I18n} from "react-redux-i18n";
import uuid from "uuid";
import { Tooltip, OverlayTrigger } from "react-bootstrap";
import SocialAvatar from "../../../../core/ui/components/social-avatar";


class SummaryInformation extends React.Component {
    render(){
        let getTranslation = (key) => {
            return "Accounts.edit.tabs."+key;
        };
        let getTooltip=(title)=>(
            <Tooltip id={uuid.v4()}>
                {title}
            </Tooltip>
        );
        let {settings,information, groups}=this.props;
        let titleStyle={
            width: "70px",
            transform: "translateY(10px)"
        };
        return (
            <div className="col-md-5 account-summary-profile">
                <div className="panel">
                    <div className="panel-body slim">
                        <div className="row">
                            <div className="col-md-7">
                                <div className="row">
                                    <div className="col-md-12">
                                        <h4><Translate value={getTranslation("summary.information.profile")}/></h4>
                                    </div>
                                    <div className="col-md-6">
                                        <div className="form-avatar">
                                            <SocialAvatar avatar={information.avatar}
                                                          name={(information.name || "") + " " + (information.lastName || "")}
                                                          size="50"/>
                                            <div className="title" style={titleStyle}>
                                                {(information.name || "") + " " + (information.lastName || "")}
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-md-6">
                                        <div className="form-group">
                                            <label htmlFor="informationName">
                                                <Translate value={getTranslation("settings.form.accountIs.title")}/>
                                            </label>
                                            <div className="form-control-static">
                                                <OverlayTrigger placement="top" overlay={getTooltip(<Translate value={getTranslation("settings.form.accountIs.brand")} />)}>
                                                    <div className={"account-type-icon "+ (settings.type.brand?(settings.type.influencer?"both":"brand"):"not")}>
                                                        {settings.type.brand?<Translate value={getTranslation("settings.form.accountIs.badges.brand")}/>:<i className="wb-close"/>}
                                                        <div className="name">
                                                            <Translate value={getTranslation("settings.form.accountIs.brand")} />
                                                        </div>
                                                    </div>
                                                </OverlayTrigger>
                                                <OverlayTrigger placement="top" overlay={getTooltip(<Translate value={getTranslation("settings.form.accountIs.influencer")} />)}>
                                                    <div className={"account-type-icon "+ (settings.type.influencer?(settings.type.brand?"both":"influencer"):"not")}>
                                                        {settings.type.influencer?<Translate value={getTranslation("settings.form.accountIs.badges.influencer")}/>:<i className="wb-close"/>}
                                                        <div className="name">
                                                            <Translate value={getTranslation("settings.form.accountIs.influencer")} />
                                                        </div>
                                                    </div>
                                                </OverlayTrigger>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-5 ">
                                <h4><Translate value={getTranslation("summary.information.campaigns.title")}/></h4>
                                <div className="row campaigns">
                                    <div className="col-md-4">
                                        <div className="count">
                                            19
                                            <div className="title">
                                                <Translate value={getTranslation("summary.information.campaigns.label.total")}/>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-md-4">
                                        <div className="count">
                                            19
                                            <div className="title">
                                                <Translate value={getTranslation("summary.information.campaigns.label.created")}/>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-md-4">
                                        <div className="count">
                                            19
                                            <div className="title">
                                                <Translate value={getTranslation("summary.information.campaigns.label.partOf")}/>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-4">
                                <h4><Translate value={getTranslation("summary.information.areas")}/></h4>
                                <div className="category-list">

                                </div>
                            </div>
                            <div className="col-md-4">
                                <h4><Translate value={getTranslation("summary.information.categories")}/></h4>
                                <div className="category-list">

                                </div>
                            </div>
                            <div className="col-md-4">
                                <h4><Translate value={getTranslation("summary.information.specialty")}/></h4>
                                <div className="category-list">

                                </div>
                            </div>
                            {/*
                            <div className="col-md-4">
                                <h4><Translate value={getTranslation("summary.information.groups")}/></h4>
                                <div className="group-list">
                                    {groups.map((g, i)=>(
                                        <div className="group" key={i}>{g}</div>
                                    ))}
                                </div>
                            </div>
                            */}
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default SummaryInformation;
