import React from "react";
import InfiniteCalendar from "react-infinite-calendar";
import TimePicker from "react-times";
import {Translate, I18n} from "react-redux-i18n";

class PostTimeDate extends React.Component {
    constructor() {
        super();

        let date = moment();
        this.state = {
            time: date,
            quantum: date.format("A")
        };

        this.onTimeChange = this.onTimeChange.bind(this);
        this.onTimeQuantumChange = this.onTimeQuantumChange.bind(this);
        this.onCalendarChange = this.onCalendarChange.bind(this);
        this.saveDate = this.saveDate.bind(this);
        this.getCurrentDate =  this.getCurrentDate.bind(this);
    }

    componentWillMount() {
        let time = new Date();
        let currentState = this.props.store ? this.props.store.getState() : null;
/*
         if (currentState && currentState.form["post-create"] && currentState.form["post-create"].values && currentState.form["post-create"].values.date) {
            time = currentState.form["post-create"].values.date;
        } */
        if(this.props.input && this.props.input.value){
             time=this.props.input.value;
        } else {
            let wrapped = moment(time);
            let remainder = 5 - wrapped.minute() % 5;
            time = wrapped.add(remainder, "minutes").toDate()
        }

        let date = moment(time);
        this.setState({
            time: date,
            quantum: date.format("A")
        })
    }

    onTimeChange(time) {
        let values = time.split(":"),
            hours = Number(values[0]),
            minutes = Number(values[1]);
        if (this.state.quantum === "PM") {
            hours += 12;
        }
        let newDate = moment(this.state.time).startOf("day").add(hours, "hours").add(minutes, "minutes");

        this.setState({time: newDate});
        if(this.props.autoSave){
            this.saveDate();
        }
    }

    onTimeQuantumChange(timeQuantum) {
        this.setState({quantum: timeQuantum});
        if(this.props.autoSave){
            this.saveDate();
        }
    }

    onCalendarChange(date) {
        const currentDate = this.state.time;
        const hours = currentDate.hours();
        const minutes = currentDate.minutes();
        const newTime = moment(date).hour(hours).minutes(minutes);

        this.setState({
            time: newTime
        });
        if(this.props.autoSave){
            this.saveDate();
        }
    }

    saveDate() {
        if(this.props.close){
            this.props.close();
        }
        this.props.input.onChange(this.state.time.toDate());
    }
    getCurrentDate(){
        let date = moment();
        if(this.state && this.state.time){
            date = moment(this.state.time);
        }

        return date;
    }
    render() {
        let getTranslation = (key) => {
            return "Agenda.publish.modal." + key;
        };
        return (
            <div className={(this.props.open ? "open" : "") + " panel date-panel panel-modal"}>
                <div className="panel-body container-fluid padding-20">
                    <div className="row">
                        <div className="col-md-6">
                            {this.props.open ? <InfiniteCalendar minDate={new Date()} onSelect={this.onCalendarChange}
                                                                 selectedDate={this.state.time}
                                                                 rowHeight={35}
                                                                 className="ibol-calendar"/> : ""}
                        </div>
                        <div className="col-md-6 date-controls">
                            <TimePicker timeMode="12"
                                        withoutIcon={true}
                                        onTimeChange={this.onTimeChange}
                                        onTimeQuantumChange={this.onTimeQuantumChange}
                                        timeQuantum={this.state.quantum}
                                        time={this.state.time.format("hh:mm")}/>

                            <p>{moment.tz.guess().split("_").join(" ")}</p>
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="time-zone">
                                        <div className="location"><Translate value={getTranslation("cities.mexico")}/></div>
                                        <div
                                            className="time">{this.getCurrentDate().tz('America/Mexico_City').format('hh:mm A')}</div>
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="time-zone">
                                        <div className="location"><Translate value={getTranslation("cities.newYork")}/></div>
                                        <div
                                            className="time">{this.getCurrentDate().tz('America/New_York').format('hh:mm A')}</div>
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="time-zone">
                                        <div className="location"><Translate value={getTranslation("cities.london")}/></div>
                                        <div
                                            className="time">{this.getCurrentDate().tz('Europe/London').format('hh:mm A')}</div>
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="time-zone">
                                        <div className="location"><Translate value={getTranslation("cities.losAngeles")}/></div>
                                        <div
                                            className="time">{this.getCurrentDate().tz('America/Los_Angeles').format('hh:mm A')}</div>
                                    </div>
                                </div>
                            </div>
                            {!this.props.autoSave && (
                                <a className="btn btn-primary pull-right" onClick={this.saveDate}>
                                    <Translate value={getTranslation("time.save")}/>
                                </a>
                            )}
                        </div>


                    </div>
                </div>
            </div>
        )
    }
}

export default PostTimeDate;